#header-main {
	text-align: center;
	background: #666;
	nav {
		float: none;
		display: inline-block;
		padding: 20px 0;
		input {
			float: none;
			display: inline-block;
			vertical-align: middle;
			width: auto;
		}
		button {
			float: none;
			display: inline-block;
			vertical-align: middle;
			background-color: #3498DB;
			color: #FFF;
			border: none;
			height: 34px;
			&:hover {
				background: darken(#3498DB, 20%);
				color: darken(#FFF, 20%);
			}
			&:focus {
				outline: none;
			}
		}
	}
}

#main-content {
	float: left;
	width: 100%;
	background: aliceblue;
	.container {
		background: #CCC;
		padding-top: 20px;
		.memes {
			float: left;
			width: 100%;
			.cols {
				text-align: center;
				min-height: 250px;
				@media only screen and (max-width: 479px) {
					min-height: 195px;
				}
				@media only screen and (min-width: 480px) and (max-width: 767px) {
					min-height: 250px;
				}
				div {
					min-height: 150px;
					display: inline-block;
					@media only screen and (max-width: 379px) {
						min-height: 100px;
					}
					@media only screen and (min-width: 380px) and (max-width: 479px) {
						min-height: 125px;
					}
					@media only screen and (min-width: 480px) and (max-width: 767px) {
						min-height: 175px;
					}
					@media only screen and (min-width: 768px) and (max-width: 991px) {
						min-height: 200px;
					}
					@media only screen and (min-width: 992px){
						min-height: 215px;
					}
					audio {
						display: none;
					}
					img {
						max-width: 100%;
						height: auto;
						max-height: 200px;
					}
				}
				a {
					float: left;
					width: 100%;
				}	
			}
		}
	}
}

